  </div>
</template>
<script>
  export default {
    data () {
      return {
        value1: 0
      }
    }
  }
</script>
<style>
  #header {
    font-size: x-large;
    background-color: #ffffff;
    color: #c94ff0;
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
  }
  #content {
    background-color: #f08beb;
    color: #e9f0eb;
  }
</style>



